
* {
    margin: 0;
    padding: 0;

}

body {
    height: 100vh;
    background-image: url(/React-Weather-App/static/media/fog-1535201_1920.4e72ab63.jpg);
    background-position: 0px -300px;
    background-size: cover;
    overflow: hidden;
}

.app input {
    position: absolute;
    top: 20%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 60%;
    border: none;
    border-bottom: 2px solid black;
    background-color: transparent;
    font-size: 40px;
}

.app ::-webkit-input-placeholder {
    color: black;
    ;
    text-align: center;
}

.app button {
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 30px;
    padding: 10px 50px;
    background-color: black;
    color: white;
    border: 3px solid chartreuse;
    transition: 0.3s;
}

.app button:hover {
    background-color: white;
    color: black;
}

.app .result {

    margin-top: 50vh;
    text-align: center;
    font-size: 35px;
}

h3,
h4 {
    color: black;
}

@media (max-width:361px) {
    .app button {
        font-size: 20px;
        text-transform: uppercase;
        font-weight: bold;
    }

    h3,
    h4 {
        font-size: 21px;
    }
}
